*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-color: #f1ded0;
  font-family: 'Karla', sans-serif;

}
.wel{
  justify-content: space-between;
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 90%;
  
}
.bg-mc {
  background-color: #0b2434;
  max-width: 400px;
  height: 420px;
  margin: auto;
  margin-top: 4%;
  padding: 29px;
  overflow: hidden;
}
.main {
  height: 100%;
  background: #f5f5f5;
  border-radius: 5px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 5px;
  flex-direction: column;
  overflow:hidden;
  position: relative;box-shadow: 0 2px 2px rgba(0,0,0,.15);
}

.title {
  font-size: 40px;
  margin: 0;
}

.instructions {
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  margin-top: 0;
  text-align: center;
  padding: 0 10px;
}
.die-box {
  /* background: #59E391; */
  background-color: white;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
  border-radius: 3.81277px;
  height: 45.84000015258789px;
  width: 45.84000015258789px;
  border-radius: 3.8127658367156982px;
}
.die-box > h2 {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  font-size: 1.8em;
  padding: 7px 0;
  font-weight: 700;
}
.die-boxes {
  display: grid;
  grid-template-columns: auto auto auto auto auto;
  /* row-gap: 15px;
  column-gap: 20px;'' */
  gap: 13px;
}
.btnN {
  width: 200px;
}
button {
  font-family: 'Karla', sans-serif;
  height: 35.84000015258789px;
  width: 92.15999603271484px;
  background: #5035ff;
  box-shadow: 0px 3.2px 7.68px rgba(0, 0, 0, 0.18);
  border-radius: 3.81277px;
  border: none;
  line-height: 19px;
  font-weight: 700;
  font-size: 1.2em;
  color: #fff;
  letter-spacing: 2px;
  cursor: pointer;
  margin-top: 10px;
  /* position: relative;
  top: 54px; */
}
.btnR:focus{
  outline: none;
}
.btnR:active{
  box-shadow: inset 5px 5px 10px -3px rgba(0, 0, 0, 0.7);

}
.die-box.green {
  background: #59E391;
}
.showbtn{
  position: absolute;
transform: translate(170% ,-420%);
width: 70px;
font-size: 0.8em;
}

.start{
  border: none;
  width: 140px;
  padding: 10px;
  height: 35px;
  border-radius: 3px;
  background-color: #5035ff;
  color: #fff;
  margin-top: -40px;
}
.Time-div{
  position: absolute;
  transform: translate(-6.3em , 9em);}
.Time-div > p{
  display: inline-block;
}
.titledd{
  text-align: center;
  background-color: #070808;
  color: white;
}
.titledd h1{
  line-height: 2em;
}