.popmain {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 1;
  background-color: aqua;
  overflow: auto;
  transition: 1s ease-in-out;
  visibility: hidden;
  padding: 5px;
  margin: 3px;
  transform: translateY(-20em);
}
.popmain.activ {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}
.popmain >img{
  margin: 3px;
  cursor: pointer;
}
.scoreD{
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 10px;
}
.scordata {
	border: aliceblue 1px solid;
	margin: 10px;
	padding: 0;
	margin: 20px 0 0 0;
	padding: 10px;
}

.scoreD > h1 {
  font-weight: 700;
  letter-spacing: 0.5px;
  text-align: center;
}
.scoreD p {
  margin-top: 10px;
  font-weight: 500;
}